@import "../../global.scss";

.menu {
  width: 180px;
  height: 99.1%;
  position: fixed;
  top: 1vh;
  left: -300px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all .5s ease;
  overflow:visible !important;

  &.active {
    left: .1%;
  }

  .icon{
    font-size: 30px;
    margin-right: 15px;
    align-items: center;
  }
  .homeicon{
    font-size: 35px;
    margin-right: 12px;
  }

  ul {
    margin: 0;
    padding-left: 13px;
    // padding: 0;
    list-style: none;
    font-size: 1.35vw;
    font-weight: 300;
    // color: rgb(149, 225, 235);
    width: 100%;
    @include tall{
      font-size: 2vh;
    }
    @include tablet{
      font-size: 2.2vw;
    }
    @include mobile{
    font-size: 4vw;
    }


    li {
      display: flex;
      align-items: center;
      margin-bottom: 3.3vh;
      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
        transition: all .1s ease;
        font-weight: 400;
      }
      &:hover {
        font-size: x-large;
      }
    }
  }

  .top{
    // margin-top: 14vh;
    margin-top: 10vh;

    margin-right: 15%;
    padding-left: 35%;
    margin-bottom: 25%;
  }
  .bottom{
    margin-top: 19vh;
  }
}